.subtotal
{
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 100px;
    padding:20px;
    background-color: #f3f3f3;
    border:1px solid #dddddd;
    border-radius: 3px;
    position: sticky;
    top:5rem;
}
.subtotal > button
{
    background:#f0c14b;
    
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #9c7e31;
    color: #111;
    cursor: pointer;
}